@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family:'poppins', sans-serif;
}
/*Menu de navegaci√≥n*/

.nav{
  width: 100%;
  position: fixed;
  height: 8vh;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: black;
  
}
.nav__title{
  display: flex;
  align-items: center;
}
.nav__title h1{
  font-size: 25px;
  font-weight: bold;
  margin-left: 15px;
  color: #fff;
  margin-left: 10px;
}
.nav__title img{
  width: 50px;
  height: 50px;
  margin-left: 10px;
}
.nav__hamburguer{
  width: 30px;
  height: 50%;
  background-image: url("../src/assets/icon-menu.svg");
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
  margin-right: 20px;

  z-index: 100;
  transition: background-image .3s;
}

.nav__hamburguer--open{
  background-image: url("../src/assets/icon-close-menu.svg");
}

.nav__overlay{
  background-color: #000000a7;
  position: fixed;
  inset: 0;

  opacity: 0;
  pointer-events: none;
  transition: .3s opacity;
}

.nav__overlay--show{
  opacity: 1;
  pointer-events: unset;
}


.nav__menu{
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  background-color: #000;
  width: 65%;
  max-width: 400px;
  padding: 0;
  list-style: none;
  overflow: auto;
  padding: 70px 0;
}

.nav__item{
  width: 80%;
  /* background-color: #000; */
  margin: 0 auto;
  color: #fff;
  cursor: pointer;
  padding: 20px 0;
}
.nav__arrow{
  transform: translate(-90deg);
}
.nav__item:has(.nav__parent){
  padding: 0;
}

.nav__inner{
  list-style: none;
  padding: 0;
  width: 80%;
  margin: 0 auto;
  display: grid;
  gap: 1.5em;
  overflow: hidden;
  height: 0;
  transition: height .4s;
}

.nav__parent{
  display: grid;
  align-items: center;
  grid-auto-flow: column;
  grid-auto-columns: max-content;
  gap: 1.3em;
  padding: 20px 0;
}

.nav__link{
  text-decoration: none;
  color: #fff;
}

/*Homepage*/
.homepage{
  display: flex;
  align-items: center;
  width: 100%;
  height: 92vh;
  background-image: linear-gradient(
    0deg,
    rgba(0,0,0,0.5),
    rgba(0,0,0,0.5)
  ),url(./assets/darling.jpg);
  background-size: cover;
  background-position: center;
}
.homepage p{
  color: white;
  font-size: 30px;
}
@media (min-width:768px) {
  .nav{
      gap: 2em;
      overflow: visible;
  }

  .nav__hamburguer{
      display: none;
  }

  .nav__overlay{
      position: unset;
      background-color: transparent;
      opacity: 1;
      pointer-events: unset;
      flex: 1;
      
  }

  .nav__menu{
      position: unset;
      width: 100%;
      max-width: unset;
  
      padding: 0;
      overflow: unset;

      display: flex;
      align-items: center;
      gap: 1em;
  }

  .nav__item{
      width: max-content;
      margin: 0;
      padding: 0;

      position: relative;
  }
  

  .nav__inner{
      position: absolute;
      left: 0;
      padding: 0;
      width: max-content;
      /* background-color: #000; */
      height: auto;
      background-color: #000;

      padding: 2em;

      z-index: 1000;

      border-radius: 10px;
      box-shadow: 0 0 10px 0 rgba(0, 0, 0, .5);

      transition: opacity .3s;
      opacity: 0;
      pointer-events: none;
  }

  .nav__inner--show{
      opacity: 1;
      pointer-events: unset;
  }


  .nav__login{
      width: max-content;
      margin: 0;
      margin-left: auto;
  }

  .nav__login--border{
      margin: 0;
      padding: 0 1.5em;
  }

}